import { useCallback } from 'react'
import { Contract } from 'web3-eth-contract'
import { useWallet } from '@binance-chain/bsc-use-wallet'
import { ethers } from 'ethers'

export const approve = async (lpContract : Contract, masterChefContract: Contract, account: string | null) => {
  return lpContract.methods
    .approve(masterChefContract.options.address, ethers.constants.MaxUint256)
    .send({ from: account })
}

// cakeContract useCake() 
// moneyWheelContract useMoneyWheel()
export const useMoneyWheelApprove = (tokenContract : Contract, moneywheelContract: Contract) => {
  const { account }: { account: string | null } = useWallet()
  // const cakeContract = useCake()
  // const moneyWheelContract = useMoneyWheel()

  const handleApprove = useCallback(async () => {
    try {
      const tx = await approve(tokenContract, moneywheelContract, account)
      return tx
    } catch (e) {
      return false
    }
  }, [account, tokenContract, moneywheelContract])

  return { onApprove: handleApprove }
}