import React, { useState } from "react";
import { Contract } from 'web3-eth-contract'
import BigNumber from 'bignumber.js'
import { makeStyles, Theme, createStyles } from '@material-ui/core/styles';
import Grid from '@material-ui/core/Grid'
import Paper from '@material-ui/core/Paper';
import MoneyWheel from './MoneyWheel'
import BalanceCard from './BalanceCard';
import BetCard from './BetCard'
import Divider from '@material-ui/core/Divider';

interface moneyWheelBscProps {
    contract: Contract
    token: Contract
    tokenBalance: string
    soundUrl: string
  }

const useStyles = makeStyles((theme: Theme) =>
  createStyles({
        root: {
          flexGrow: 1,
        },
        rootBalance: {
          width: 'fit-content',
          border: `1px solid ${theme.palette.divider}`,
          borderRadius: theme.shape.borderRadius,
          backgroundColor: theme.palette.background.paper,
          color: theme.palette.text.secondary,
          '& svg': {
            margin: theme.spacing(1.5),
          },
          '& hr': {
            margin: theme.spacing(0, 0.5),
          }
        },
        paper: {
          padding: theme.spacing(2),
          textAlign: 'center',
          color: theme.palette.text.secondary,
        },
        paperLeft: {
          minHeight: 800,
        },
        title: {
          fontSize: 14,
        },
        cardRoot: {
          minWidth: 275,
        },
        minMaxPaper: {
          padding: theme.spacing(2),
          textAlign: 'center',
          color: theme.palette.text.secondary,
        },
        balancePaper: {
          padding: theme.spacing(2),
          textAlign: 'center',
          color: theme.palette.text.secondary,
          borderRadius: 22,
          backgroundColor: '#8cc761',
        },
        large: {
          width: theme.spacing(7),
          height: theme.spacing(7),
        },
        license: {
          paddingTop: theme.spacing(1),
        }
    }),
);

  const MoneyWheelBsc: React.FC<moneyWheelBscProps> = ({ contract, token, tokenBalance, soundUrl}) => {
    const [mustSpin, setMustSpin] = useState(false)
    const [prizeNumber, setPrizeNumber] = useState(0)
    const classes = useStyles();
    const balance = new BigNumber(tokenBalance)
    const max = balance

    const onResult = (lastResult: any) => {
      setPrizeNumber(lastResult.spin)
      setMustSpin(true)
    }
    
    const onStopping = () => {
      setMustSpin(false)
    }
    return(<>
          <div className={classes.root} >
            <Grid container alignItems="stretch" spacing={2}>
              <Grid item style={{display: 'flex'}} alignItems="stretch" xs={12} md={6}>
                <Paper className={classes.paper} style={{display: 'flex', justifyContent: 'space-between', flexDirection: 'column', width: '100%'}}>
                  <MoneyWheel prizeNumber={prizeNumber} mustSpin={mustSpin} onStopping={onStopping} soundUrl={soundUrl}/>
                  <Divider light />
                  <a href="https://defifusion.io" className={classes.license}>Licensed by DeFiFusion</a>
                </Paper>
              </Grid>
              <Grid item style={{display: 'flex'}} alignItems="stretch" xs={12} md={6}>
                <Grid container  spacing={2}>
                  {/* <Grid item xs={12} md={6} lg={6}>
                    <MinMaxCard/>
                  </Grid> */}
                  <Grid item  xs={12} md={12} lg={12}>
                    <BalanceCard balance={tokenBalance}/>
                  </Grid>
                  <Grid item style={{display: 'flex'}} alignItems="stretch" xs={12} md={12}>
                  <Paper className={classes.paper} style={{display: 'flex', justifyContent: 'space-between', flexDirection: 'column', width: '100%'}}>
                    <BetCard tokenBalance={balance} max={new BigNumber(max)} onResult={onResult} tokenContract={token} moneyWheelContract={contract}/>
                  </Paper>
                </Grid>
                </Grid>
              </Grid>

            </Grid>
          </div>
          </>


        // <Grid container alignItems="stretch" spacing={2}>
        //     <Grid item style={{display: 'flex'}} xs={12} sm={12} md={12} lg={5} xl={3}>
        //         <Card style={{display: 'flex', justifyContent: 'space-between', flexDirection: 'column', width: '100%'}}>
        //           <CardHeader>
        //             <Typography className={classes.title} color="textSecondary" gutterBottom>
        //               Word of the Day
        //             </Typography>
        //           </CardHeader>
        //           <CardContent>
        //             <MoneyWheel prizeNumber={prizeNumber} mustSpin={mustSpin} onStopping={onStopping}/>
                    
        //           </CardContent>
        //           <Divider variant="fullWidth" />
        //           <CardActions>
        //             <Button size="small">Learn More</Button>
        //           </CardActions>
        //         </Card> 
        //     </Grid>
        //     <Grid style={{display: 'flex'}} item xs={12} sm={12} md={12} lg={7} xl={9}>
        //     <Card style={{display: 'flex', justifyContent: 'space-between', flexDirection: 'column', width: '100%'}}>
        //           <CardHeader>
        //             <Typography className={classes.title} color="textSecondary" gutterBottom>
        //               Word of the Day
        //             </Typography>
        //           </CardHeader>
        //           <CardContent>
        //           <BetCard max={new BigNumber(tokenBalance)} onResult={onResult} tokenContract={token} moneyWheelContract={contract}/>
        //           </CardContent>
        //           <Divider variant="fullWidth" />
        //           <CardActions>
        //             <Button size="small">Learn More</Button>
        //           </CardActions>
        //         </Card> 
        //     </Grid>
        // </Grid>
    )
  }

  export default MoneyWheelBsc