import React from 'react';
import { makeStyles, Theme, createStyles } from '@material-ui/core/styles';
import Paper from '@material-ui/core/Paper';
import TextField from '@material-ui/core/TextField';
import Button from '@material-ui/core/Button';

interface BetInputProps
{
  onSelectMax?: (evt : any) => void
  onChange: (evt: any) => void
  name: string
  value: string
  multiplier: string
}

const useStyles = makeStyles((theme: Theme) =>
  createStyles({
    root: {
      display: 'flex',
      alignItems: 'center',
      width: 'auto',
      margin: theme.spacing(2),
    },
    margin: {
      height: 40,
      margin: 4,
      backgroundColor: '#eff4f5',
      boxShadow: 'none',
      color: '#3bb790',
      fontWeight: 'bold',
      "&:hover": {
        backgroundColor: "#eff4f5"
      },
    },
    input: {
      flex: 1,
    },
    bet: {
      alignSelf: 'stretch',
      background: '#8cc761',
      flex: '0 0 auto',
      textAlign: 'center',
      width: '50px',
      borderTopLeftRadius: 22,
      borderBottomLeftRadius: 22,
      color: 'white',
      padding: 8,
      fontSize: 24,
      paddingLeft: 15,
      userSelect: 'none',
    },
    inputBox: {
      height: 50,
      paddingLeft: 10,
      borderTop: 'solid 1px #8cc761',
      borderBottom: 'solid 1px #8cc761',
      borderRight: 'solid 1px #8cc761',
      borderTopRightRadius: 15,
      borderBottomRightRadius: 15,
    }
  }),
);

const BetInput: React.FC<BetInputProps> = ({multiplier, onSelectMax, onChange, name, value}) => {
  const classes = useStyles();

  const MaxButton = () => (
    <Button name={name} variant="contained" color="primary" className={classes.margin} onClick={onSelectMax}>MAX</Button>
    )

  return (
    <>
    <Paper elevation={0} className={classes.root}>
      <div className={classes.bet}>{multiplier}</div>
      <div className={classes.inputBox}>
      <TextField
         className={classes.input}
         value={value}
         onChange={onChange}
         name={name}
         InputProps={{endAdornment: <MaxButton />, disableUnderline: true}}
       />
      </div>
    </Paper>
       </>
  );
}

export default React.memo(BetInput)