import React, { useState } from 'react'
import { Contract } from 'web3-eth-contract'
import { makeStyles, Theme, createStyles } from '@material-ui/core/styles';
import Modal from '@material-ui/core/Modal';
import Button from '@material-ui/core/Button'
import { useBetMoneyWheel } from './hooks/useBetMoneyWheel'

export interface MoneyWheelBet {
    val1: string
    val3: string
    val5: string
    val10: string
    val20: string
    val50: string
}

interface ConfirmBetModalProps{
    moneyWheelContract: Contract
    bet: MoneyWheelBet
    onResult: (lastResult: any) => void
    disabled: boolean
}

const useStyles = makeStyles((theme: Theme) =>
  createStyles({
    paper: {
      position: 'absolute',
      width: 500,
      backgroundColor: theme.palette.background.paper,
      
      boxShadow: theme.shadows[5],
      padding: theme.spacing(2, 4, 3),
    },
    modal: {
      display: 'flex',
      alignItems: 'center',
      justifyContent: 'center',
    },
    button: {
      margin: theme.spacing(1),
      backgroundColor: '#3bb790',
      fontWeight: 'bold',
      borderRadius: 12,
      "&:hover": {
        backgroundColor: "#3bb790"
      },
    }
  }),
);


const ConfirmBetModal: React.FC<ConfirmBetModalProps> = ({ moneyWheelContract, bet, onResult, disabled }) => {
    const classes = useStyles();
    const [pendingTx, setPendingTx] = useState(false)
    const [open, setOpen] = React.useState(false);

    const { onBetMoneyWheel } = useBetMoneyWheel(moneyWheelContract)
    
    let totalBet = 0
    Object.entries(bet).map(([_, value]) => {
      totalBet += Number(value)
    })
    
    const onConfirm = async () => {
        const lastResult = await onBetMoneyWheel(bet)
        onResult(lastResult)
    }

    const handleOpen = () => {
        setOpen(true)
    };
      
    const handleClose = () => {
        setOpen(false)
        setPendingTx(false)
      };
    
    const body = (
      <div className={classes.paper}>
        <h2 id="simple-modal-title">Are you sure you want to place your bet?</h2>
        <h3>Total bet: {totalBet}</h3>
        <p id="simple-modal-description">
          <div>
            <Button variant="contained" onClick={handleClose}>
              Cancel
            </Button>
            <Button
                variant="contained"
                color="primary"
                className={classes.button}
                disabled={pendingTx}
                onClick={async () => {
                  setPendingTx(true)
                  await onConfirm()
                  setPendingTx(false)
                  handleClose()
                }}>
                {pendingTx ? "Pending Confirmation" : "Confirm" }
            </Button>
          </div>
        </p>
      </div>
    );

    return (<>
        <Modal
          open={open}
          className={classes.modal}
          onClose={handleClose}
          aria-labelledby="simple-modal-title"
          aria-describedby="simple-modal-description">
          {body}
        </Modal>
        <Button fullWidth disabled={disabled} className={classes.button} variant="contained" color="primary" onClick={handleOpen}>
            Place Bet
        </Button>
    </>
    )
}
export default ConfirmBetModal